import React from 'react';
import Container from '@material-ui/core/Container';

import vector from './../Assets/vector.jpg';
import AirtelPrepaidCard from '../Components/AirtelPrepaidCard';
import AirtelPostpaidCard from '../Components/AirtelPostpaidCard';
import FullWidthTabs from '../Components/FullWidhTab';
import AirtelBlackCard from '../Components/AirtelBlack';
import AirtelSmallCard from '../Components/AirtelSmallCard';
import AirtelThanksCard from '../Components/AirtelThanksCard';
import TextButton from '../Components/BackToTopTextButton';
import SingleLineImageList from '../Components/ImageList';
import IconButtons from '../Components/IconButtons';
import CopyrightIcon from '@mui/icons-material/Copyright';
import FooterButton from '../Components/FooterButton';
import FooterButton1 from '../Components/FooterButton1';
import FooterButton2 from '../Components/FooterButton2';
import FooterButton3 from '../Components/FooterButton3';
import FooterButton4 from '../Components/FooterButton4';
import CarouselStepper from '../Components/CarouselStepper';
import Grid from '@material-ui/core/Grid';
import { Hidden } from '@material-ui/core';
import DeskImgMediaCard from '../Components/DeskCards';
import DeskImgMediaCard2 from '../Components/DeskCards2';
import DeskImgMediaCard3 from '../Components/DeskCards3';
import DeskImgMediaCard4 from '../Components/DeskCards4';
import DeskImgMediaCard5 from '../Components/DeskCards5';

const divStyle = {
  color: '#720001',
  fontSize: 30,
  textDecorationLine: 'underline',
  fontWeight: 'bold'
};

export default function Home() {
  return (
    <React.Fragment>
        <Grid container spacing={3}>
        <Container>
        
          <center>
      
        <Grid item xs={12} sm={12} md={4} lg={4} xl={4}>
          <img src={vector} alt="vector" height="100%" width="100%" />
          </Grid>

        
          <Grid item xs={12} sm={12} md={8} lg={8} xl={8}>  
          <FullWidthTabs/>  
          </Grid>
         
          <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
          <CarouselStepper/>
          </Grid>
          
          <Grid item xs={12} sm={6} md={6} lg={6} xl={6}>
          <AirtelPrepaidCard/>
          </Grid><br/>
          
          <Grid item xs={12} sm={6} md={6} lg={6} xl={6}>
          <AirtelPostpaidCard/><br/>
          </Grid>

          <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
          <AirtelBlackCard/><br/>
          </Grid>
          
          <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
          <AirtelSmallCard/>
          </Grid><br/>
         
          <div style={divStyle}>
                    Entertainment
        </div>
        <br/>
        <Grid container spacing={3} justifyContent ='center'>
            <Hidden only={['xs', 'sm']}>
            <Grid item  md={2} lg={2} xl={2}>
        <DeskImgMediaCard/>
        </Grid>
        </Hidden> 

        <Hidden only={['xs', 'sm']}>
            <Grid item  md={2} lg={2} xl={2}>
        <DeskImgMediaCard2/>
        </Grid>
        </Hidden> 

        <Hidden only={['xs', 'sm']}>
            <Grid item  md={2} lg={2} xl={2}>
        <DeskImgMediaCard3/>
        </Grid>
        </Hidden> 

        <Hidden only={['xs', 'sm']}>
            <Grid item  md={2} lg={2} xl={2}>
        <DeskImgMediaCard4/>
        </Grid>
        </Hidden> 

        <Hidden only={['xs', 'sm']}>
            <Grid item  md={2} lg={2} xl={2}>
        <DeskImgMediaCard5/>
        </Grid>
        
        </Hidden> 
        </Grid>

          <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
          <Hidden only={['md', 'lg', 'xl']}>
            <SingleLineImageList/>
            </Hidden>
            </Grid>
          

          <Grid item xs={12} sm={12} md={12} lg={6} xl={6}>
          <AirtelThanksCard/>
          </Grid>
         
         
          <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
         <TextButton/>
         </Grid>
     
         </center>
         </Container> 
         
          
         
        
        
        <footer className="App-footer">   
        
        <IconButtons/> 
       
        <FooterButton/>
        <FooterButton1/>
        <FooterButton2/>
        <FooterButton3/>
        <FooterButton4/>
        
        <p>
        <CopyrightIcon/> 2021 Airtel India. All Rights Reserved.
        </p> 
      
        </footer>
        </Grid>
                
    </React.Fragment>
  );
}