import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ImageList from '@material-ui/core/ImageList';
import ImageListItem from '@material-ui/core/ImageListItem';
import ImageListItemBar from '@material-ui/core/ImageListItemBar';
import xstreamsmol from './../Assets/xstreamsmol.png';
import wynksmol from './../Assets/wynksmol.png';
import shawsmol from './../Assets/shawsmol.png';
import disneysmol from './../Assets/disneysmol.png';
import Grid from '@material-ui/core/Grid';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper,
  },
  imageList: {
    flexWrap: 'nowrap',
    
    transform: 'translateZ(0)',
  },
  title: {
    color: theme.palette.primary.light,
  },
  titleBar: {
    background:
      'linear-gradient(to top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)',
  },
}));


  
  const itemData = [
    {
      img1: xstreamsmol,
       
      },
  ];
  const itemData2 = [
    {
    img2: wynksmol,
    
    },
   
  ];
  const itemData3 = [
    {
    img3: shawsmol,
    
    },
   
  ];
  const itemData4 = [
    {
    img4: disneysmol,
     
    },
];
const itemData5 = [
  {
  img4: xstreamsmol,
   
  },
   
  
  ];
 
export default function SmallImageList() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
     <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
      <ImageList className={classes.imageList} cols={3.5} rowHeight="100">
        {itemData.map((item) => (
          <ImageListItem key={item.img1}>
            <img src={item.img1} alt={item.title} />
            <ImageListItemBar
              title={item.title}
              classes={{
                root: classes.titleBar,
                title: classes.title,
              }}
              
            />
          </ImageListItem>
        ))}
        {itemData2.map((item) => (
          <ImageListItem key={item.img2}>
            <img src={item.img2} alt={item.title} />
            <ImageListItemBar
              title={item.title}
              classes={{
                root: classes.titleBar,
                title: classes.title,
              }}
              
            />
          </ImageListItem>
        ))}
        {itemData3.map((item) => (
          <ImageListItem key={item.img3}>
            <img src={item.img3} alt={item.title} />
            <ImageListItemBar
              title={item.title}
              classes={{
                root: classes.titleBar,
                title: classes.title,
              }}
              
            />
          </ImageListItem>
        ))}
        {itemData4.map((item) => (
          <ImageListItem key={item.img4}>
            <img src={item.img4} alt={item.title} />
            <ImageListItemBar
              title={item.title}
              classes={{
                root: classes.titleBar,
                title: classes.title,
              }}
              
            />
          </ImageListItem>
        ))}
        {itemData5.map((item) => (
          <ImageListItem key={item.img5}>
            <img src={item.img5} alt={item.title} />
            <ImageListItemBar
              title={item.title}
              classes={{
                root: classes.titleBar,
                title: classes.title,
              }}
              
            />
          </ImageListItem>
        ))}
      </ImageList>
      </Grid>
    </div>
  );
}