import * as React from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import { CardActionArea } from '@mui/material';
import Typography from '@mui/material/Typography';
import PlatinumButton from './PlatinumButton';
import { Grid } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import grey from './../Assets/grey airtel.png';

const useStyles = makeStyles({
  root: {
    minWidth: 275,
    background: 'linear-gradient(to right bottom, #19202E, #1C2432, #232C3C, #3F4F63, #485B70 )',
  },
  bullet: {
    display: 'inline-block',
    margin: '0 0px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 20,
    color:'#F5ED95'
  },
  pos: {
    marginBottom: 12,
    fontSize: 31,
    color:'#F5ED95'
  },
  outline: {
    color: '#F5ED95'
  },
  div: {
    position: 'relative',
    top: '60px',
    right: '90px',

  }
});

export default function PlatinumCard() {
  const classes = useStyles();
  

  return (
    
    
    <Card className={classes.root} variant="outlined" style={{ borderRadius: 9 }}>
      <CardActionArea>
        
        <CardContent>
        <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
          <Typography className={classes.pos} color="textSecondary" gutterBottom >
            Try out Airtel Thanks Platinum
          </Typography></Grid>
        <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
        <Typography className={classes.title}  align='left'>
          To get amazing rewards
          </Typography></Grid>
          <br/><br/>
          <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
          <PlatinumButton /> 
          </Grid>
        
         
        </CardContent>
      </CardActionArea>
      
    </Card>
    
  );
}
